.text-c {
  text-align: center;
}
.text-l {
  text-align: left;
}
.text-r {
  text-align: right;
}
.none {
  display: none;
}
.absolute {
  position: absolute;
}
.fixed {
  position: fixed;
}
.relative {
  position: relative;
}
#backToTop {
  width: 40px;
  height: 40px;
  line-height: 52px;
  overflow: hidden;
  z-index: 9999;
  display: none;
  position: fixed;
  bottom: 50px;
  right: 12px;
  background-color: #d0ddd5;
  color: #6f8b7c;
  text-align: center;
  font-size: 30px;
  text-decoration: none;
  border-radius: 20px;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
#backToTop:hover {
  background-color: #323f38;
  color: #d0ddd5;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.m-0-a {
  margin: 0 auto;
