.parallax {
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-perspective: 1px;
  perspective: 1px;
}
.parallax-layer {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 100vh;
  white-space: nowrap;
}
.parallax-layer .one {
  color: #ccc;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
}
.parallax-layer .two {
  color: #bbb;
  -webkit-transform: translateZ(-0.25px);
  transform: translateZ(-0.25px);
}
.parallax-layer .three {
  color: #aaa;
  -webkit-transform: translateZ(-0.5px);
  transform: translateZ(-0.5px);
}
.parallax-layer .four {
  color: #999;
  -webkit-transform: translateZ(-0.75px);
  transform: translateZ(-0.75px);
}
.parallax-layer .five {
  color: #888;
  -webkit-transform: translateZ(-1px);
  transform: translateZ(-1px);
}
.parallax-layer .six {
  color: #777;
  -webkit-transform: translateZ(-1.25px);
  transform: translateZ(-1.25px);
}
.parallax-layer .seven {
  color: #666;
  -webkit-transform: translateZ(-1.5px);
  transform: translateZ(-1.5px);
}
.cityFade-enter {
  opacity: 0.01;
}
.cityFade-enter.cityFade-enter-active {
  opacity: 1;
  transition: opacity 1000ms ease-in;
}
.cityFade-leave {
  opacity: 1;
}
.cityFade-leave.cityFade-leave-active {
  opacity: 0.01;
  transition: opacity 300ms ease-in;
}
.cityFade-appear {
  opacity: 0.01;
}
.cityFade-appear.cityFade-appear-active {
  opacity: 1;
  transition: opacity 1.5s ease-in;
}
